  .btn {
    position: relative;
    width: 200px;
    height: 40px;
    margin: 0 auto;
    line-height: 40px;
    text-align: center;
    cursor: pointer;
  }
  
  .btn::before {
    content: '';
    position: absolute;
    top: -100px;
    left: 0;
    right: 0;
    width: 1px;
    height: 80px;
    margin: auto;
    background: #fff;
    visibility: hidden;
    opacity: 0;
  }
  
  .btn:hover::before {
    -webkit-animation: sword .5s;
            animation: sword .5s;
  }
  
  @-webkit-keyframes sword {
    0% {top: -100px; visibility: hidden; opacity: 0;}
    20% {visibility: visible; opacity: 1;}
    100% {top: 100px; visibility: hidden; opacity: 0;}
  }
  
  @keyframes sword {
    0% {top: -100px; visibility: hidden; opacity: 0;}
    20% {visibility: visible; opacity: 1;}
    100% {top: 100px; visibility: hidden; opacity: 0;}
  }
  
  .btn span {
    display: block;
    color: #fff;
    font-size: 14px;
    text-decoration: none;
  }
  
  .btn span::before,
  .btn span::after {
    content: '';
    position: absolute;
    top: 0;
    width: 100px;
    height: 38px;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    -webkit-transition: all .5s 0s;
    transition: all .5s 0s;
  }
  
  .btn span::before {
    left: 0;
    border-left: 1px solid #fff;
  }
  
  .btn span::after {
    right: 0;
    border-right: 1px solid #fff;
  }
  
  .btn span:hover::before {
    left: -20px;
    -webkit-transition: all .5s .2s;
    transition: all .5s .2s;
    opacity: 0;
  }
  
  .btn span:hover::after {
    right: -20px;
    -webkit-transition: all .5s .2s;
    transition: all .5s .2s;
    opacity: 0;
  }
  
  
